import React, { Component } from 'react';
import { Row, Col, Card} from 'antd';
import ProjectTask from './ProjectTasks/ProjectTask';
import {connect} from "react-redux"
import { updateProjectTaskDrag } from '../../actions/backlogActions';


class Backlog extends Component {

    constructor(){
        super();
        this.state = {
            dragData:{},
            brotherStatus:null
        }
    }

    onDrop(e){
        if(e.target.innerHTML==="TODO"){
            this.state.dragData.status = '1';
        }else if(e.target.innerHTML==="FINISHED"){
            this.state.dragData.status = '3';
        }else if(e.target.innerHTML==="IN PROGRESS"){
            this.state.dragData.status = '2';
        }
        // console.log(this.state.brotherStatus);
        this.props.updateProjectTaskDrag(this.state.dragData.projectIdentifier,
                this.state.dragData.projectSequence,this.state.dragData)
        this.setState({});
    }

    setDragData = (dragData)=>{
        this.setState({dragData});
    }

    dragToBrotherHandler=()=>{

    }

    setbrotherStatus = (brotherStatus)=>{
        this.setState({brotherStatus},()=>{
            this.state.dragData.status=this.state.brotherStatus;
            this.props.updateProjectTaskDrag(this.state.dragData.projectIdentifier,
                this.state.dragData.projectSequence,this.state.dragData);
            this.setState({});
        });
    }

    render() {
        const {project_tasks_props} = this.props;
        let taskArr = [[],[],[]];
        project_tasks_props.map(project_task => {
            if(project_task.status==="1" || project_task.status===1){
                taskArr[0].push(<ProjectTask key={project_task.id} project_task_prop={project_task} 
                    setbrotherStatus={this.setbrotherStatus} setDragData={this.setDragData}/>);
            }
            if(project_task.status==="2" || project_task.status===2){
                taskArr[1].push(<ProjectTask key={project_task.id} project_task_prop={project_task} 
                    setbrotherStatus={this.setbrotherStatus} setDragData={this.setDragData}/>);
            }
            if(project_task.status==="3" || project_task.status===3){
                taskArr[2].push(<ProjectTask key={project_task.id} project_task_prop={project_task} 
                    setbrotherStatus={this.setbrotherStatus} setDragData={this.setDragData}/>);
            }
        })

    return (
        <div className="container">
        <Row gutter={10}>
            <Col className="gutter-row" span={8} align="center">
                <Card hoverable title="TODO" bordered={true} align="left"
                headStyle={{background: '#b7b7b7' ,color:"white"}}
                style={{background:'#ebebeb'}}
                onDragOver={(event)=>{event.preventDefault();return false;}}
                onDrop={this.onDrop.bind(this)}>
                    {taskArr[0]}
                </Card>
            </Col>

            <Col className="gutter-row" span={8} align="center">
                <Card hoverable title="IN PROGRESS" bordered={true} align="left" 
                headStyle={{background: '#0092ff',color:"white"}}
                style={{background:'#E0F2FF'}}
                onDragOver={(event)=>{event.preventDefault();return false;}}
                onDrop={this.onDrop.bind(this)}>
                    {taskArr[1]}
                </Card>
                </Col>
            <Col className="gutter-row" span={8} align="center">
                <Card hoverable title="FINISHED" bordered={true} align="left" 
                headStyle={{background: '#5cb85c',color:"white"}}
                style={{background:'#D8FFD6'}}
                onDragOver={(event)=>{event.preventDefault();return false;}}
                onDrop={this.onDrop.bind(this)}>
                    {taskArr[2]}
                </Card>
            </Col>
        </Row>
        </div> 
        );
  }
}

export default connect(null,{updateProjectTaskDrag}) (Backlog);
