import React, { Component,Fragment } from 'react';
import {Button} from 'antd';
import { Link } from 'react-router-dom';
import {AppstoreAddOutlined} from '@ant-design/icons';
import Backlog from './Backlog';
import PropTypes from "prop-types"
import { connect } from 'react-redux';
import { getBacklog } from '../../actions/backlogActions';

class ProjectBoard extends Component {

    constructor(){
        super();
        this.state = {
            errors:{}
        };
    }


    componentDidMount(){
        const {id} = this.props.match.params;
        this.props.getBacklog(id);
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors){
            this.setState({errors:nextProps.errors});
        }
    }

    render() {
        const {id} = this.props.match.params;
        const {project_tasks} = this.props.backlog;
        const {errors} = this.state;
        
        let BoardContent;

        const boardAlgorithm = (errors) => {
            if (errors.projectNotFound) {
            return (
                <div className="alert alert-danger text-center" role="alert">
                {errors.projectNotFound}
                </div>
            );
            } else {
            return (
                <div className="container">
                    <Link to={`/addProjectTask/${id}`}>
                        <Button type="primary" shape="round" icon={<AppstoreAddOutlined />} size='large'>Create a Project Task</Button>
                    </Link>
                    <br />
                    <hr />
                    <div className="alert alert-info text-center" role="alert">
                        No Project Tasks on this board
                    </div>
                </div>
            );
            }
        };

        BoardContent = boardAlgorithm(errors);



        return (
            <Fragment>
            {
                project_tasks.length<1 ? boardAlgorithm(errors):(
                    <div className="container">
                    <Link to={`/addProjectTask/${id}`}>
                        <Button type="primary" shape="round" icon={<AppstoreAddOutlined />} size='large'>Create a Project Task</Button>
                    </Link>
                    <br />
                    <hr />
                    <Backlog project_tasks_props={project_tasks}/>
                    </div>)
            }
            </Fragment>
        )
  }
}

ProjectBoard.propTypes = {
    backlog:PropTypes.object.isRequired,
    getBacklog:PropTypes.func.isRequired,
    errors:PropTypes.object.isRequired
}

const mapStateToProps = state =>({
    backlog: state.backlog,
    errors: state.errors
})

export default connect(mapStateToProps,{getBacklog})(ProjectBoard);