import React, { Component } from 'react';
import { Form, Input,DatePicker, Button, Typography,Radio} from 'antd';
import {SendOutlined} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';
import { updateProjectTask,getProjectTask } from '../../../actions/backlogActions';
import PropTypes from "prop-types";
import moment from "moment";

const { Title } = Typography;
const { TextArea } = Input;
const optionsPriority = [
    { label: 'High', value: '1' },
    { label: 'Medium', value: '2' },
    { label: 'Low', value: '3' },
  ];

const optionsStatus = [
    { label: 'TODO', value: '1' },
    { label: 'IN PROGRESS', value: '2' },
    { label: 'Done', value: '3' },
  ];

class UpdateProjectTask extends Component {

    constructor(props){
        super(props);

        this.state = {
            id: "",
            projectSequence: "",
            summary: "",
            acceptanceCriteria: "",
            status: "",
            priority: "",
            dueDate: "",
            projectIdentifier: "",
            create_At: "",
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onFinish = this.onFinish.bind(this);
    }

    formRef = React.createRef();

    getDateTime(time) {
      return time ? moment(time, 'YYYY-MM-DD') : null
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors){
            this.setState({errors:nextProps.errors});
        }
        const {
            id,
            projectSequence,
            summary,
            acceptanceCriteria,
            status,
            priority,
            dueDate,
            projectIdentifier,
            create_At
          } = nextProps.projectTask;
      
          this.setState({
            id,
            projectSequence,
            summary,
            acceptanceCriteria,
            status,
            priority,
            dueDate,
            projectIdentifier,
            create_At
          },()=>{
              this.formRef.current.setFieldsValue({
                  'projsummary': this.state.summary,
                  'projacccri': this.state.acceptanceCriteria,
                  'Status': this.state.status.toString(),
                  'Priority': this.state.priority.toString(),
                  'Due Date': this.getDateTime(this.state.dueDate)
           })
          });
    }

    componentDidMount() {
        const { backlog_id,pt_id } = this.props.match.params;
        this.props.getProjectTask(backlog_id,pt_id);
    }

    onChange(e){
        this.setState({[e.target.name]:e.target.value});
    }

    onFinish(values){
        const { backlog_id, pt_id } = this.props.match.params;
        
        const UpdateProjectTask = {
            id: this.state.id,
            projectSequence: this.state.projectSequence,
            summary: this.state.summary,
            acceptanceCriteria: this.state.acceptanceCriteria,
            status: this.state.status,
            priority: this.state.priority,
            dueDate: this.state.dueDate,
            projectIdentifier: this.state.projectIdentifier,
            create_At: this.state.create_At
          };
        this.props.updateProjectTask(backlog_id,pt_id,UpdateProjectTask,this.props.history);
    }

  render() {
    const { backlog_id, pt_id } = this.props.match.params;
    return (
        <div className="container">
            <div className="row">
                <div className="col-md-8 m-auto">
                    <Link to={`/ProjectBoard/${backlog_id}`}>
                        <Button>
                            Back to Project Board
                        </Button>
                    </Link>
                    <Title align='center'>Update Project Task</Title>
                    <p className="lead text-center">Project Name: {backlog_id} | Project Task ID:{pt_id}</p>
                    <Form 
                        ref={this.formRef}
                        name="basic"
                        labelCol={{
                            span: 8,
                        }}
                        wrapperCol={{
                            span: 8,
                        }}
                        initialValues={{
                            remember: true,
                        }}
                        autoComplete="off"
                        onFinish={this.onFinish}>
                        <Form.Item rules={[{ required: true, message: 'Please input a summary!' }]} name="projsummary" label="Project Name">
                            <Input placeholder="Project Task summary" name='summary'
                            onChange={this.onChange}
                            value={this.state.summary}/>
                        </Form.Item>
                        <Form.Item rules={[{ required: true, message: 'Please input a project description!' }]} name="projacccri" label="Accept Criteria">
                            <TextArea rows={5} placeholder="Acceptance Criteria" name='acceptanceCriteria'
                            onChange={this.onChange}
                            value={this.state.acceptanceCriteria}/>
                        </Form.Item>

                        <Form.Item rules={[{ required: true, message: 'Please input a date!' }]} name="Due Date" label="Due Date">
                            <DatePicker name="dueDate" value={this.state.dueDate} onOk={this.onChange}/>
                        </Form.Item>
                        
                        <Form.Item name="Priority" label="Priority">
                            <Radio.Group
                                name = "priority"
                                options={optionsPriority}
                                optionType="button"
                                value={this.state.priority}
                                onChange={this.onChange}
                            />
                        </Form.Item>

                        <Form.Item name="Status" label="Status">
                            <Radio.Group
                                name = "status"
                                options={optionsStatus}
                                optionType="button"
                                value={this.state.status}
                                onChange={this.onChange}
                            />
                        </Form.Item>
                        <Form.Item 
                            wrapperCol={{
                            xs: { span: 24, offset: 0 },
                            sm: { span: 16, offset: 8 },
                        }}>
                            <Button shape='round' type="primary" icon={<SendOutlined />} size='large' htmlType="submit">Submit</Button>
                        </Form.Item>
                    </Form>
                </div>
            </div>
        </div>
    );
  }
}

UpdateProjectTask.propTypes = {
    getProjectTask: PropTypes.func.isRequired,
    updateProjectTask: PropTypes.func.isRequired,
    projectTask: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
}

  
const mapStateToProps = state => ({
    projectTask: state.backlog.project_task,
    errors: state.errors
});

export default connect(mapStateToProps,{getProjectTask,updateProjectTask})(UpdateProjectTask);