import React, { Component, Fragment } from 'react';
import { Card, Divider } from 'antd';
import { EditOutlined, DeleteOutlined  } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import PropTypes from "prop-types"
import {connect} from "react-redux"
import { deleteProjectTask, updateProjectTask } from '../../../actions/backlogActions';

const backgroundcolor = {
    1:{
        3:'#b7b7b7',
        2:'#858585',
        1:'#5E5E5E'
    },
    2:{
        3:'#B0E2FF',
        2:'#6d9eeb',
        1:'#0092ff'
    },
    3:{
        3:'#79ea79',
        2:'#43d943',
        1:'#5cb85c'
    }
}

const priority={
    1:"High",
    2:"Medium",
    3:"Low",
}



class ProjectTask extends Component {

    componentDidMount(){
        const {project_task_prop,setDragData} = this.props;
        this.project_task_prop=project_task_prop;
        this.setDragData = setDragData;
    }

    project_task_prop={};

    delete = (project_id, pt_id)=>{
        this.props.deleteProjectTask(project_id, pt_id);
    }

    onDragStart = (e)=>{
        this.setDragData(this.project_task_prop);
    }

    onDrop = (e)=>{
        if(this.project_task_prop?.status==='1' || 
        this.project_task_prop?.status==='2'||
        this.project_task_prop?.status==='3'){
            this.props.setbrotherStatus(this.project_task_prop.status);
        }
    }

    render() {
        const {project_task_prop} = this.props;
        return (
            <Fragment>
            <Card hoverable title={project_task_prop.summary} bordered={true}
            headStyle={{background: backgroundcolor[project_task_prop.status][project_task_prop.priority] ,color:"white"}}
            actions={[
                <Link to={`/updateProjectTask/${project_task_prop.projectIdentifier}/${project_task_prop.projectSequence}`}>
                    <EditOutlined key="edit"/>
                </Link>,
                <DeleteOutlined key="ellipsis" onClick={this.delete.bind(this, project_task_prop.projectIdentifier,project_task_prop.projectSequence)}/>,
            ]}
            onDragStart={this.onDragStart.bind(this)}
            draggable
            onDrop={this.onDrop}
            >
                <ul >
                    <li>Priority: {priority[project_task_prop.priority]}</li>
                    <li>Accept Criteria: {project_task_prop.acceptanceCriteria}</li>
                    <li>Due Date: {project_task_prop.dueDate.substring(0,10)}</li>
                </ul>
            </Card>
            <Divider></Divider>
            </Fragment>

        );
    }
}

ProjectTask.propTypes = {
    deleteProjectTask: PropTypes.func.isRequired,
    setDragData:PropTypes.func.isRequired
}

export default connect(null,{deleteProjectTask,updateProjectTask}) (ProjectTask);