import React, { Component } from 'react';
import {Form,Input,Button} from 'antd';
import {SendOutlined} from '@ant-design/icons';
import { connect } from 'react-redux';
import { createNewUser } from '../../actions/securityActions';
import PropTypes from "prop-types"

class Register extends Component {
    constructor(){
        super();
        this.state = {
            username:"",
            fullname:"",
            password:"",
            confirmPassword:""

        }
        this.onChange = this.onChange.bind(this);
        this.onFinish = this.onFinish.bind(this);
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors){
            this.setState({errors:nextProps.errors});
        }
    }

    onChange = (e)=>{
        this.setState({[e.target.name]:e.target.value})
    }

    componentDidMount(){
        if(this.props.security.validToken){
            this.props.history.push("/dashboard");
        }
    }

    onFinish(){
        const newUser = {
            username:this.state.username,
            fullName:this.state.fullname,
            password:this.state.password,
            confirmPassword:this.state.confirmPassword
          };
          console.log(newUser);
        this.props.createNewUser(newUser,this.props.history);
    }

  render() {
    const {errors} = this.state;

    return (
        <div className="register">
        <div className="container">
            <div className="row">
                <div className="col-md-8 m-auto">
                    <h1 className="display-4 text-center">Sign Up</h1>
                    <p className="lead text-center">Create your Account</p>
                    <p align='center' style={{color:'red'}}>{errors?.username}</p>
                    <p align='center' style={{color:'red'}}>{errors?.fullname}</p>
                    <p align='center' style={{color:'red'}}>{errors?.password}</p>
                    <Form 
                        ref={this.formRef}
                        name="basic"
                        labelCol={{
                            span: 8,
                        }}
                        wrapperCol={{
                            span: 8,
                        }}
                        initialValues={{
                            remember: true,
                        }}
                        autoComplete="off"
                        onFinish={this.onFinish}>
                        <Form.Item rules={[{ required: true, message: 'Please input a name!' }]} name="Username" label="Userame">
                            <Input placeholder="Username" name='username'
                            onChange={this.onChange} value={this.state.name}/>
                        </Form.Item>
                        <Form.Item rules={[{ required: true, message: 'Please input full name!' }]} name="Fullname" label="Fullname">
                            <Input placeholder="Full Name" name='fullname'
                            onChange={this.onChange} value={this.state.fullname}/>
                        </Form.Item>


                        <Form.Item
                            name="password"
                            label="Password"
                            rules={[
                            {
                                required: true,
                                message: 'Please input your password!',
                            },
                            ({ getFieldValue }) => ({
                                validator(_, value) {
                                if (value.length>=6) {
                                    return Promise.resolve();
                                }
                                return Promise.reject(new Error('The password should longer than 6 characters!'));
                                },
                            })
                            ]}
                            hasFeedback
                        >
                            <Input.Password onChange={this.onChange} name="password" value={this.state.password}/>
                        </Form.Item>

                        <Form.Item
                            name="confirm"
                            label="Confirm Password"
                            dependencies={['password']}
                            hasFeedback
                            rules={[
                            {
                                required: true,
                                message: 'Please confirm your password!',
                            },
                            ({ getFieldValue }) => ({
                                validator(_, value) {
                                if (!value || getFieldValue('password') === value) {
                                    return Promise.resolve();
                                }
                                return Promise.reject(new Error('The two passwords that you entered do not match!'));
                                },
                            }),
                            ]}
                        >
                            <Input.Password onChange={this.onChange} name="confirmPassword" value={this.state.confirmPassword}/>
                        </Form.Item>
                        <Form.Item 
                                wrapperCol={{
                                xs: { span: 24, offset: 0 },
                                sm: { span: 16, offset: 8 },
                            }}>
                                <Button shape='round' type="primary" icon={<SendOutlined />} size='large' htmlType="submit">Submit</Button>
                            </Form.Item>
                    </Form>
                </div>
            </div>
        </div>
    </div>
        );
  }
}

Register.propTypes = {
    createNewUser: PropTypes.func.isRequired,
    security:PropTypes.object.isRequired,
    errors:PropTypes.object.isRequired
}

const mapStateToProps = state =>({
    errors: state.errors,
    security: state.security
})

export default connect(mapStateToProps,{createNewUser})(Register);