import React, { Component} from 'react';
import { Form, Input,DatePicker, Button,Typography} from 'antd';
import {SendOutlined} from '@ant-design/icons';
import PropTypes from "prop-types";
import {connect} from "react-redux";
import { createProject } from '../../actions/projectActions';


const { Title } = Typography;
const { TextArea } = Input;


class AddProject extends Component {
    constructor(){
        super();
        this.state = {
            projectName:"",
            projectIdentifier:"",
            description:"",
            start_date:"",
            end_date:"",
            errors:{}
        };
        this.onChange = this.onChange.bind(this);
        this.onFinish = this.onFinish.bind(this);
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors){
            this.setState({errors:nextProps.errors});
        }
    }

    onChange(e){
        this.setState({[e.target.name]:e.target.value});
    }

    onFinish(values){
        
        const newProject = {
            projectName:this.state.projectName,
            projectIdentifier:this.state.projectIdentifier,
            description:this.state.description,
            start_date:values['Start Date'].format('yyyy-MM-DD'),
            end_date:values['End Date'].format('yyyy-MM-DD')
        };

        this.props.createProject(newProject,this.props.history);

    }

    render() {
        const {errors} = this.state;
        return (
        <React.Fragment>
            <Title level={2} align='center'>Create Project form</Title>
            <p align='center' style={{color:'red'}}>{errors.projectName}</p>
            <p align='center' style={{color:'red'}}>{errors.projectIdentifier}</p>
            <p align='center' style={{color:'red'}}>{errors.description}</p>
            <p align='center' style={{color:'red'}}>{errors.start_date}</p>
            <p align='center' style={{color:'red'}}>{errors.end_date}</p>

            <Form 
            name="basic"
            labelCol={{
                span: 8,
            }}
            wrapperCol={{
                span: 8,
            }}
            initialValues={{
                remember: true,
            }}
            autoComplete="off"
            onFinish={this.onFinish}>
                <Form.Item rules={[{ required: true, message: 'Please input a project name!' }]} name="projname" label="Project Name">
                    <Input placeholder="Project Name" name='projectName' value={this.state.projectName}
                    onChange={this.onChange}/>
                </Form.Item>
                {/* <p styles={{align:'center',color:'red',fontSize:'50%'}}>{errors.projectName}</p> */}

                <Form.Item  rules={[{ required: true, message: 'Please input a 5 digit project id!' }]} name="projid" label="ID">
                    <Input placeholder="Unique Project ID(5 Digits)" name='projectIdentifier' value={this.state.projectIdentifier}
                    onChange={this.onChange}/>
                </Form.Item>
                {/* <p align='center'  color='red' font-size='50%'>{errors.projectIdentifier}</p> */}

                <Form.Item rules={[{ required: true, message: 'Please input a project description!' }]} label="Description">
                    <TextArea rows={5} placeholder="Description" name='description' value={this.state.description}
                    onChange={this.onChange}/>
                </Form.Item>

                <Form.Item rules={[{ required: true, message: 'Please input a date!' }]} name="Start Date" label="Start Date">
                    <DatePicker name="start_date" value={this.state.start_date} onOk={this.onChange}/>
                </Form.Item>

                <Form.Item rules={[{ required: true, message: 'Please input a date!' }]} name="End Date" label="End Date">
                    <DatePicker name="end_date" value={this.state.end_date} onOk={this.onChange}/>
                </Form.Item>

                
                <Form.Item 
                    wrapperCol={{
                    xs: { span: 24, offset: 0 },
                    sm: { span: 16, offset: 8 },
                }}>
                    <Button shape='round' type="primary" icon={<SendOutlined />} size='large' htmlType="submit">Submit</Button>
                </Form.Item>
            </Form>
        </React.Fragment>
        );
        }
}

AddProject.propTypes = {
    createProject: PropTypes.func.isRequired,
    errors:PropTypes.object.isRequired
  };

  const mapStateToProps = state => ({
      errors: state.errors
  });

export default connect(mapStateToProps, {createProject})(AddProject);