import React, { Component } from 'react';
import {Form,Input,Button} from 'antd';
import {SendOutlined} from '@ant-design/icons';
import { connect } from 'react-redux';
import { login } from '../../actions/securityActions';
import PropTypes from "prop-types"

class Login extends Component {

    constructor(){
        super();
        this.state = {
            username:"",
            password:"",
            errors:{}
        }
        this.onChange = this.onChange.bind(this);
        this.onFinish = this.onFinish.bind(this);
    }

    componentDidMount(){
        if(this.props.security.validToken){
            this.props.history.push("/dashboard");
        }
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors){
            this.setState({errors:nextProps.errors});
        }
        if(nextProps.security.validToken){
            this.props.history.push("/dashboard");
        }
    }

    onChange = (e)=>{
        this.setState({[e.target.name]:e.target.value})
    }

    onFinish(){
        const newUser = {
            username:this.state.username,
            password:this.state.password,
            confirmPassword:this.state.confirmPassword
          };
          console.log(newUser);
        this.props.login(newUser);
    }

  render() {
    const {errors} = this.state;
    return (    
    <div className="login">
    <div className="container">
        <div className="row">
            <div className="col-md-8 m-auto">
                <h1 className="display-4 text-center">Log In</h1>
                <p align='center' style={{color:'red'}}>{errors?.password ? "Sorry username and password do not match":null}</p>
                <Form 
                        ref={this.formRef}
                        name="basic"
                        labelCol={{
                            span: 8,
                        }}
                        wrapperCol={{
                            span: 8,
                        }}
                        initialValues={{
                            remember: true,
                        }}
                        autoComplete="off"
                        onFinish={this.onFinish}>
                    <Form.Item rules={[{ required: true, message: 'Please input your username!' }]} name="Username" label="Username">
                        <Input placeholder="Username" name='username'
                        onChange={this.onChange} value={this.state.username}/>
                    </Form.Item>
                    <Form.Item rules={[{ required: true, message: 'Please input Password!' }]} name="Password" label="Password">
                        <Input placeholder="Password" name='password'
                        onChange={this.onChange} type={"password"} value={this.state.password}/>
                    </Form.Item>
                    <Form.Item 
                        wrapperCol={{
                        xs: { span: 24, offset: 0 },
                        sm: { span: 16, offset: 8 },
                    }}>
                        <Button shape='round' type="primary" icon={<SendOutlined />} size='large' htmlType="submit">Submit</Button>
                    </Form.Item>
                </Form>
            </div>
        </div>
    </div>
</div>);
  }
}

Login.propTypes = {
    login: PropTypes.func.isRequired,
    errors: PropTypes.object.isRequired,
    security: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    errors: state.errors,
    security: state.security
});

export default connect(mapStateToProps,{login})(Login);