import React, { Component } from "react";
import { getProject, createProject } from "../../actions/projectActions";
import { Form, Input,DatePicker, Button,Typography} from 'antd';
import {SendOutlined} from '@ant-design/icons';
import PropTypes from "prop-types";
import { connect } from "react-redux";
import moment from "moment";


const { Title } = Typography;
const { TextArea } = Input;


class UpdateProject extends Component {
  //set state
  constructor() {
    super();

    this.state = {
      id: "",
      projectName: "",
      projectIdentifier: "",
      description: "",
      start_date: "",
      end_date: "",
      errors: {}
    };
    this.onChange = this.onChange.bind(this);
    this.onFinish = this.onFinish.bind(this);
  }

  formRef = React.createRef();

  getDateTime(time) {
    return time ? moment(time, 'YYYY-MM-DD') : null
  }


  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
    const {
      id,
      projectName,
      projectIdentifier,
      description,
      start_date,
      end_date
    } = nextProps.project;

    this.setState({
      id,
      projectName,
      projectIdentifier,
      description,
      start_date,
      end_date
    },()=>{
        this.formRef.current.setFieldsValue({
            'projname': this.state.projectName,
            'projid': this.state.projectIdentifier,
            'Description': this.state.description,
            'Start Date': this.getDateTime(this.state.start_date),
            'End Date': this.getDateTime(this.state.end_date)
     })
    });
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getProject(id, this.props.history);
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onFinish(values){
        
    const updateProject = {
        id: this.state.id,
        projectName:this.state.projectName,
        projectIdentifier:this.state.projectIdentifier,
        description:this.state.description,
        start_date:values['Start Date'].format('yyyy-MM-DD'),
        end_date:values['End Date'].format('yyyy-MM-DD')
    };

    this.props.createProject(updateProject,this.props.history);

}

  render() {
    console.log(this.state)
    const { errors } = this.state;
    return (
      <div className="project">
        <div className="container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <h5 className="display-4 text-center">Update Project form</h5>
              <hr />
              <Form 
              ref={this.formRef}
            name="basic"
            labelCol={{
                span: 8,
            }}
            wrapperCol={{
                span: 8,
            }}
            initialValues={{
                remember: true,
            }}
            autoComplete="off"
            onFinish={this.onFinish}>
                <Form.Item rules={[{ required: true, message: 'Please input a project name!' }]} name="projname" label="Project Name">
                    <Input placeholder="Project Name" name='projectName' defaultValue={this.state.projectName}
                    onChange={this.onChange}/>
                </Form.Item>
                {/* <p styles={{align:'center',color:'red',fontSize:'50%'}}>{errors.projectName}</p> */}

                <Form.Item  rules={[{ required: true, message: 'Please input a project id!' }]} name="projid" label="ID">
                    <Input placeholder="Unique Project ID" name='projectIdentifier' value={this.state.projectIdentifier}
                    onChange={this.onChange} disabled/>
                </Form.Item>
                {/* <p align='center'  color='red' font-size='50%'>{errors.projectIdentifier}</p> */}

                <Form.Item rules={[{ required: true, message: 'Please input a project description!' }]} label="Description">
                    <TextArea rows={5} placeholder="Description" name='description' value={this.state.description}
                    onChange={this.onChange}/>
                </Form.Item>

                <Form.Item rules={[{ required: true, message: 'Please input a date!' }]} name="Start Date" label="Start Date">
                    <DatePicker name="start_date" value={this.state.start_date} onOk={this.onChange} disabled/>
                </Form.Item>

                <Form.Item rules={[{ required: true, message: 'Please input a date!' }]} name="End Date" label="End Date">
                    <DatePicker name="end_date" value={this.state.end_date} onOk={this.onChange}/>
                </Form.Item>

                
                <Form.Item 
                    wrapperCol={{
                    xs: { span: 24, offset: 0 },
                    sm: { span: 16, offset: 8 },
                }}>
                    <Button shape='round' type="primary" icon={<SendOutlined />} size='large' htmlType="submit">Submit</Button>
                </Form.Item>
            </Form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

UpdateProject.propTypes = {
  getProject: PropTypes.func.isRequired,
  createProject: PropTypes.func.isRequired,
  project: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  project: state.project.project,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getProject, createProject }
)(UpdateProject);