import React, { Component } from 'react';
import { Form, Input,DatePicker, Button, Typography,Radio,Row,Col} from 'antd';
import {SendOutlined} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';
import { addProjectTask } from '../../../actions/backlogActions';
import PropTypes from "prop-types";

const { Title } = Typography;
const { TextArea } = Input;
const optionsPriority = [
    { label: 'High', value: '1' },
    { label: 'Medium', value: '2' },
    { label: 'Low', value: '3' },
  ];

const optionsStatus = [
    { label: 'TODO', value: '1' },
    { label: 'IN PROGRESS', value: '2' },
    { label: 'Done', value: '3' },
  ];

class AddProjectTask extends Component {

    constructor(props){
        super(props);
        const {id} = this.props.match.params;

        this.state = {
            summary:"",
            acceptanceCriteria:"",
            status:1,
            priority:3,
            dueDate:"",
            projectIdentifier:id,
            errors:{}
        };
        this.onChange = this.onChange.bind(this);
        this.onFinish = this.onFinish.bind(this);
        this.id = id;
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors){
            this.setState({errors:nextProps.errors});
        }
    }

    onChange(e){
        console.log(e.target);
        this.setState({[e.target.name]:e.target.value},()=>{
            console.log(this.state);    
        });
    }

    onFinish(values){
        
        const newProjectTask = {
            summary:this.state.summary,
            acceptanceCriteria:this.state.acceptanceCriteria,
            status:this.state.status,
            priority:this.state.priority,
            dueDate:values['Due Date'].format('yyyy-MM-DD')
        };
        console.log(this.id,newProjectTask);
        this.props.addProjectTask(this.id,newProjectTask,this.props.history);
    }

  render() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-md-8 m-auto">
                    <Link to={`/ProjectBoard/${this.id}`}>
                        <Button>
                            Back to Project Board
                        </Button>
                    </Link>
                    <Title align='center'>Add Project Task</Title>
                    <Form 
                        name="basic"
                        labelCol={{
                            span: 8,
                        }}
                        wrapperCol={{
                            span: 8,
                        }}
                        initialValues={{
                            remember: true,
                        }}
                        autoComplete="off"
                        onFinish={this.onFinish}>
                        <Form.Item rules={[{ required: true, message: 'Please input a summary!' }]} name="projsummary" label="Project Name">
                            <Input placeholder="Project Task summary" name='summary'
                            onChange={this.onChange}
                            value={this.state.summary}/>
                        </Form.Item>
                        <Form.Item rules={[{ required: true, message: 'Please input a project description!' }]} label="Accept Criteria">
                            <TextArea rows={5} placeholder="Acceptance Criteria" name='acceptanceCriteria'
                            onChange={this.onChange}
                            value={this.state.acceptanceCriteria}/>
                        </Form.Item>

                        <Form.Item rules={[{ required: true, message: 'Please input a date!' }]} name="Due Date" label="Due Date">
                            <DatePicker name="dueDate" value={this.state.dueDate} onOk={this.onChange}/>
                        </Form.Item>
                        
                        <Form.Item name="Priority" label="Priority">
                            <Radio.Group
                                name = "priority"
                                options={optionsPriority}
                                optionType="button"
                                value={this.state.priority}
                                onChange={this.onChange}
                            />
                        </Form.Item>

                        <Form.Item name="Status" label="Status">
                            <Radio.Group
                                name = "status"
                                options={optionsStatus}
                                optionType="button"
                                value={this.state.status}
                                onChange={this.onChange}
                            />
                        </Form.Item>
                        <Form.Item 
                            wrapperCol={{
                            xs: { span: 24, offset: 0 },
                            sm: { span: 16, offset: 8 },
                        }}>
                            <Button shape='round' type="primary" icon={<SendOutlined />} size='large' htmlType="submit">Submit</Button>
                        </Form.Item>
                    </Form>
                </div>
            </div>
        </div>
    );
  }
}

AddProjectTask.propTypes = {
    addProjectTask: PropTypes.func.isRequired
}


export default connect(null,{addProjectTask})(AddProjectTask);