import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from "prop-types";
import { connect } from 'react-redux';
import {logout} from "../../actions/securityActions"
import store from '../../store';
import {UserOutlined} from '@ant-design/icons';

class Header extends Component {
    onClick = ()=>{
        this.props.logout();
        window.location.href = "/";
    }
  render() {
      const {validToken, user} = this.props.security;

      const userIsAuthenticated = (
        <div className="collapse navbar-collapse" id="mobile-nav" style={{flexDirection: "row",flex:1,justifyContent:"space-between"}}>
        <ul className="navbar-nav mr-auto" style={{justifyContent: "flex-start"}}>
            <li className="nav-item">
                <a className="nav-link" href="/dashboard">
                    Dashboard
                </a>
            </li>
        </ul>

        <ul className="navbar-nav ml-auto" style={{justifyContent: "flex-end"}}>
            <li className="nav-item" onClick={this.onClick}>
                <div className="nav-link ">
                    Sign Out
                </div>
            </li>
            <li className="nav-item">
                <Link className="nav-link " to="/dashboard">
                    <UserOutlined/>
                    {user.fullName}
                </Link>
            </li>
        </ul>
    </div>
      );

      const userIsNotAuthenticated = (
        <div className="collapse navbar-collapse" id="mobile-nav" style={{flexDirection: "row",flex:1,justifyContent:"space-between"}}>

        <ul className="navbar-nav mr-auto" style={{justifyContent: "flex-start"}}>
            <li className="nav-item">
            </li>
        </ul>
        <ul className="navbar-nav ml-auto" style={{justifyContent: "flex-end"}}>
            <li className="nav-item">
                <Link to="/Register" className="nav-link ">
                    Sign Up
                </Link>
            </li>
            <li className="nav-item">
                <Link to="/Login" className="nav-link ">
                    Login
                </Link>
            </li>
        </ul>
    </div>
      );
    return (
      <nav className="navbar navbar-expand-sm navbar-dark bg-primary mb-4">
        <div className="container" >
            <Link to="/dashboard" className="navbar-brand">
                Personal Project Management Tool
            </Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#mobile-nav">
                <span className="navbar-toggler-icon" />
            </button>
            {
                validToken ? userIsAuthenticated:userIsNotAuthenticated
            }
            
        </div>
    </nav>);
  }
}

Header.propTypes = {
    logout: PropTypes.func.isRequired,
    security: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    security: state.security
})

export default connect(mapStateToProps,{logout})(Header)