import React, { Component } from 'react';
import { Button } from 'antd';
import { DeleteOutlined, GroupOutlined, InsertRowBelowOutlined } from '@ant-design/icons';
import {Link} from "react-router-dom"
import PropTypes from "prop-types"
import {connect} from "react-redux"
import { deleteProject } from '../../actions/projectActions'; 

class ProjectItem extends Component {

    onDeleteClick = id =>{
        this.props.deleteProject(id);
    }


  render() {
      const {projectName,projectIdentifier,description} = this.props.project;
    return (
        <div className="container">
        <div className="card card-body bg-light mb-3">
            <div className="row">
                <div className="col-2">
                    <span className="mx-auto">{projectIdentifier}</span>
                </div>
                <div className="col-lg-6 col-md-4 col-8">
                    <h3>{projectName}</h3>
                    <p>{description}</p>
                </div>
                <div className="col-md-4 d-none d-lg-block">
                    <ul className="list-group">
                        <Link to={`/projectBoard/${projectIdentifier}`}>
                            <li className="list-group-item board">
                                <Button type="primary" icon={<GroupOutlined />} style={{height:'100%',width:'100%'}}>Project Board</Button>
                            </li>
                        </Link>
                        <Link to={`/updateProject/${projectIdentifier}`}>
                            <li className="list-group-item update">
                                <Button type="primary" icon={<InsertRowBelowOutlined />} style={{height:'100%',width:'100%'}}>Update Project Info</Button>
                            </li>
                        </Link>
                        <li className="list-group-item delete" onClick={this.onDeleteClick.bind(this,projectIdentifier)}>
                            <i><Button type="primary" icon={<DeleteOutlined />} danger style={{height:'100%',width:'100%'}}>Delete</Button></i>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
        );
  }
}

ProjectItem.propTypes = {
    deleteProject: PropTypes.func.isRequired
}

export default connect(null,{deleteProject}) (ProjectItem);